@use '../globals';
.navbar{
    display: flex;
    height: 3.75rem;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 globals.$margin;
    color: globals.$black;
    background-color: globals.$black;
    border-bottom: 1px solid globals.$gray;
    position: fixed;
    z-index: 44;
    h1{
        font-size: 1.875rem;
        color: globals.$primary-color;
    }
    input{
        position: absolute;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        height: 2.5rem;
        width: 30rem;
        background-color: globals.$white;
        color: globals.$black;
        padding: 0.688rem 0.938rem;
        border: none;
        border-radius: globals.$border-radius-small;
    }
    input::placeholder{
        color: globals.$black
    }
    input.user-search{
        width: 15.625rem;
        position: absolute;
        margin-left: calc(globals.$margin-big + globals.$margin-small);
    }
    .navlinks{
        display: flex;
        gap: globals.$margin-big;
        height: 100%;
        a{
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 60px;
        }
        a.active{
            border-bottom: 1px solid globals.$primary-color
        }
        svg{
            width: 40px;
            height: 40px;
        }
    }
    .login-btn-parent{
        display: flex;
        align-items: center;
        img{
            border-radius: 50%;
        }
        img.sign-off{
            margin-left: globals.$margin-small;
            width: 40px;
            height: 40px;
        }
        span{
            color: globals.$secondary-color;
        }
        button{
            margin-left: 1.375rem;
        }
    }
}



